/* Extra 'Normalization' */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Buttons */
button {
  border: none;
  background: none;
}
.btn {
  padding: 7px 10px;
  border-radius: 5px;
  border: 1px solid;
}

.btn-learn-more {
  float: right;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.btn-learn-more:hover {
  transition: all 0.25s;
}

/* Navigation/Search */

.search-nav {
  position: fixed;
  top: 20px;
  left: 47.5%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 28px;
  width: 88vw;
  height: 50px;
}

.btn-search {
  position: absolute;
  right: -20px;
  top: -5px;
  border-radius: 100%;
  width: 2rem;
  height: 2rem;
  padding: 13px;
  box-sizing: content-box;
}

.btn-search i {
  -webkit-transform: translate(-4%, 7%);
          transform: translate(-4%, 7%);
}
.nav-logo {
  float: left;
}

.nav-logo {
  height: 50px;
  padding-left: 10px;
}

.nav-logo > img {
  height: 100%;
}

/* Thumbnails View */

.container {
  display: flex;
  flex-wrap: wrap;
  padding-top: 75px;
  margin: auto;
}

.container > img.feed-thumbnail {
  width: 47vw;
  height: 47vw;
  margin: 1.5vw;
}
.container > .col-1 img {
  width: 47vw;
  height: 47vw;
  margin: 1.5vw;
}

/* Product Preview Modal */

.modal {
  z-index: 1;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s; */
}

.product-preview-modal {
  padding: 25px;
  position: fixed;
  width: 80vw;
  max-height: 90vh;
  /* border: 1px solid; */
  border-radius: 15px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0px 1px 25px;
}

.product-preview-modal .preview-header {
  width: 90%;
}
.modal-price {
  float: right;
  border-right: none;
  border-radius: 5px 0 0 5px;
}
.big-thumbnail {
  transition-delay: 0.5s;
  transition: all 0.5s;
}
.big-thumbnail {
  padding: 10px 0 10px 0px;
}
.big-thumbnail img {
  width: 80%;
  margin-left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.preview-footer {
  margin-top: 10px;
}

/* ProductDetailView.js */
.product-detail-container {
  box-sizing: border-box;
  padding: 15px;
  padding-top: 85px;
}
.product-header {
  margin-top: 15px;
  padding-left: 5px;
  padding-right: 5px;
}

.product-image img {
  width: 100%;
}
.info-bar {
  font-size: 0.95rem;
  display: flex;
  justify-content: space-evenly;
  margin-top: 13px;
  padding-top: 7px;
  padding-bottom: 6px;
  width: 100%;
  border: 1px solid;
  border-left: none;
  border-right: none;
}
.info-bar .price {
  display: inline-block;
}

.info-bar .stock-info {
  display: inline-block;
}
.product-description {
  width: 100%;
  margin-top: 15px;
  padding-left: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid;
}
.product-description em {
  font-size: 0.9rem;
  margin-top: 15px;
  margin-right: 15px;
  float: right;
}

.product-description-empty {
  margin-top: 0px;
  padding-left: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid;
}

.product-description-empty > em {
  font-size: 0.9rem;
  margin-top: 10px;
  margin-left: 3px;
  margin-right: 3px;
  display: inline-block;
  float: none;
}

* {
  font-family: Roboto, Helvetica, sans-serif;
  font-weight: 300;
}
h2 {
  font-family: "Cinzel";
  font-weight: 400;
  font-size: 1.3rem;
}

.btn {
  font-size: 0.9rem;
  font-weight: 400;
  text-transform: uppercase;
}

:root {
  --brand1: #fff426;
  --brand2: #f8b500;
  --white: white;
  --charcoal: rgb(55, 55, 55);
  --lightgrey: lightgray;
}

/* Buttons */
.btn {
  background: white;
  border-color: #d6d6d6;
}

.btn-learn-more {
  color: rgb(45, 45, 45);
  text-shadow: white 0px 0px 2px; /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    -45deg,
    #fff426 -1%,
    #f8b500 100%
  );
  background: linear-gradient(
    -45deg,
    var(--brand1) -1%,
    var(--brand2) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff426', endColorstr='#f8b500',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.btn-learn-more:hover {
  color: white;
  box-shadow: lightgray 1px 0px 5px;
}

/* Navigation/Search */

.search-nav {
  color: white;
  z-index: 1;
  background-color: #fff426;
  background-color: var(--brand1); /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    45deg,
    white 35%,
    #f8b500 85%
  );
  background: linear-gradient(
    45deg,
    var(--white) 35%,
    var(--brand2) 85%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff426', endColorstr='#f8b500',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.534);
}

.btn-search {
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.658);
  background-color: #fff426; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    45deg,
    #fff426 -1%,
    #f8b500 100%
  );
  background: linear-gradient(
    45deg,
    var(--brand1) -1%,
    var(--brand2) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fff426', endColorstr='#f8b500',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.534);
}

/* Product Preview Modal */
.modal {
  background-color: rgba(0, 0, 0, 0.5);
}
.product-preview-modal {
  background: white;
  /* border-color: grey; */
  box-shadow: black;
}

/* ProductDetailView.js */
.info-bar {
  border-color: lightgrey;
}
.info-bar .stock-info {
  border-right-color: lightgray;
  border-right-color: var(--lightgrey);
}
.price i {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}
.divider {
  color: rgb(55, 55, 55);
  color: var(--charcoal);
}

.product-description {
  border-bottom-color: lightgray;
  border-bottom-color: var(--lightgrey);
}

@media screen and (min-width: 40.5em) {
  .container {
    width: 70vw;
    margin: auto;
  }
  .product-image img {
    width: 100%;
  }
  .container > img.feed-thumbnail {
    width: 25vw;
    height: 25vw;
  }

  .search {
    padding: 20px;
    top: -10px;
  }
  .product-preview-modal {
    max-width: 600px;
  }
  .product-preview-modal .preview-header {
    padding: 5px;
    width: 80%;
  }

  h2 {
    font-size: 1.7rem;
  }
  .btn {
    font-size: 1.2rem;
  }
  .preview-desc p {
    font-size: 1.1rem;
  }
}

