/* Extra 'Normalization' */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Buttons */
button {
  border: none;
  background: none;
}
.btn {
  padding: 7px 10px;
  border-radius: 5px;
  border: 1px solid;
}

.btn-learn-more {
  float: right;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.btn-learn-more:hover {
  transition: all 0.25s;
}

/* Navigation/Search */

.search-nav {
  position: fixed;
  top: 20px;
  left: 47.5%;
  transform: translateX(-50%);
  font-size: 28px;
  width: 88vw;
  height: 50px;
}

.btn-search {
  position: absolute;
  right: -20px;
  top: -5px;
  border-radius: 100%;
  width: 2rem;
  height: 2rem;
  padding: 13px;
  box-sizing: content-box;
}

.btn-search i {
  transform: translate(-4%, 7%);
}
.nav-logo {
  float: left;
}

.nav-logo {
  height: 50px;
  padding-left: 10px;
}

.nav-logo > img {
  height: 100%;
}

/* Thumbnails View */

.container {
  display: flex;
  flex-wrap: wrap;
  padding-top: 75px;
  margin: auto;
}

.container > img.feed-thumbnail {
  width: 47vw;
  height: 47vw;
  margin: 1.5vw;
}
.container > .col-1 img {
  width: 47vw;
  height: 47vw;
  margin: 1.5vw;
}

/* Product Preview Modal */

.modal {
  z-index: 1;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  /* transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s; */
}

.product-preview-modal {
  padding: 25px;
  position: fixed;
  width: 80vw;
  max-height: 90vh;
  /* border: 1px solid; */
  border-radius: 15px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 1px 25px;
}

.product-preview-modal .preview-header {
  width: 90%;
}
.modal-price {
  float: right;
  border-right: none;
  border-radius: 5px 0 0 5px;
}
.big-thumbnail {
  transition-delay: 0.5s;
  transition: all 0.5s;
}
.big-thumbnail {
  padding: 10px 0 10px 0px;
}
.big-thumbnail img {
  width: 80%;
  margin-left: 50%;
  transform: translateX(-50%);
}

.preview-footer {
  margin-top: 10px;
}

/* ProductDetailView.js */
.product-detail-container {
  box-sizing: border-box;
  padding: 15px;
  padding-top: 85px;
}
.product-header {
  margin-top: 15px;
  padding-left: 5px;
  padding-right: 5px;
}

.product-image img {
  width: 100%;
}
.info-bar {
  font-size: 0.95rem;
  display: flex;
  justify-content: space-evenly;
  margin-top: 13px;
  padding-top: 7px;
  padding-bottom: 6px;
  width: 100%;
  border: 1px solid;
  border-left: none;
  border-right: none;
}
.info-bar .price {
  display: inline-block;
}

.info-bar .stock-info {
  display: inline-block;
}
.product-description {
  width: 100%;
  margin-top: 15px;
  padding-left: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid;
}
.product-description em {
  font-size: 0.9rem;
  margin-top: 15px;
  margin-right: 15px;
  float: right;
}

.product-description-empty {
  margin-top: 0px;
  padding-left: 5px;
  padding-bottom: 10px;
  border-bottom: 1px solid;
}

.product-description-empty > em {
  font-size: 0.9rem;
  margin-top: 10px;
  margin-left: 3px;
  margin-right: 3px;
  display: inline-block;
  float: none;
}
